.sticky-header {
  z-index: 1000;
  background-color: var(--black);
  border-bottom: 1px solid var(--white);
  position: fixed;
  top: 0;
  right: 0;
  box-shadow: 0 0 13px 6px #0000004d;
}

.sticky-header nav a {
  color: var(--white);
  padding: 10px 15px;
  text-decoration: none;
  display: block;
}

.sticky-header nav a:hover {
  font-weight: bold;
}

